                    <v-slider v-else-if="index == 1" min="0" v-bind:max="Number(input_data[index+1][13])*1.3" step="0.1"
                      thumb-label="always" :thumb-size="30" v-model="selected_city_column[index+1]"></v-slider>
                    <v-slider v-else-if="index == 2" min="0" v-bind:max="Number(input_data[index+1][13])*1.3" step="0.1"
                      thumb-label="always" :thumb-size="30" v-model="selected_city_column[index+1]"></v-slider>
                    <v-slider v-else-if="index == 3" min="0" v-bind:max="Number(input_data[index+1][13])*1.3" step="0.1"
                      thumb-label="always" :thumb-size="40" v-model="selected_city_column[index+1]"></v-slider>
                    <v-slider v-else-if="index == 4" min="0" v-bind:max="Number(input_data[index+1][13])*1.3" step="0.1"
                      thumb-label="always" :thumb-size="30" v-model="selected_city_column[index+1]"></v-slider>
                    <v-slider v-else-if="index == 5" min="0" v-bind:max="Number(input_data[index+1][13])*1.3"
                      step="0.01" thumb-label="always" :thumb-size="30" v-model="selected_city_column[index+1]">
                    </v-slider>
                    <v-slider v-else-if="index == 6" min="0" v-bind:max="Number(input_data[index+1][13])*1.3" step="0.1"
                      thumb-label="always" :thumb-size="30" v-model="selected_city_column[index+1]"></v-slider>
                    <v-slider v-else-if="index == 7" min="0" v-bind:max="Number(input_data[index+1][13])*1.3" step="0.1"
                      thumb-label="always" :thumb-size="30" v-model="selected_city_column[index+1]"></v-slider>
                    <v-slider v-else-if="index == 8" min="0" v-bind:max="Number(input_data[index+1][13])*1.3" step="0.1"
                      thumb-label="always" :thumb-size="30" v-model="selected_city_column[index+1]"></v-slider>
                    <v-slider v-else-if="index == 9" min="0" v-bind:max="Number(input_data[index+1][13])*1.3" step="0.1"
                      thumb-label="always" :thumb-size="40" v-model="selected_city_column[index+1]"></v-slider>
                    <v-slider v-else-if="index == 10" min="0" v-bind:max="Number(input_data[index+1][13])*1.3"
                      step="0.01" thumb-label="always" :thumb-size="30" v-model="selected_city_column[index+1]">
                    </v-slider>
                    <v-slider v-else-if="index == 11" min="0" v-bind:max="Number(input_data[index+1][13])*1.3"
                      step="0.01" thumb-label="always" :thumb-size="30" v-model="selected_city_column[index+1]">
                    </v-slider>
                    <v-slider v-else-if="index == 12" min="0" v-bind:max="Number(input_data[index+1][13])*1.3"
                      step="0.1" thumb-label="always" :thumb-size="45" v-model="selected_city_column[index+1]">
                    </v-slider>
                    <v-slider v-else-if="index == 13" min="0" v-bind:max="Number(input_data[index+1][13])*1.3" step="1"
                      thumb-label="always" :thumb-size="45" v-model="selected_city_column[index+1]"></v-slider>
                    <v-slider v-else-if="index == 14" min="0" v-bind:max="Number(input_data[index+1][13])*1.3" step="1"
                      thumb-label="always" :thumb-size="30" v-model="selected_city_column[index+1]"></v-slider>
                  </div>
                  <div style="padding : 5%; width: 40%">
                    <v-text-field v-model="selected_city_column[index+1]">
                      {{selected_city_column[index+1]}}</v-text-field>
