      </v-layout>
      <v-layout wrap>
        <v-flex xs12 sm12 md12 v-show="isShow">
          <div style="height: 500px; width: 100%; overflow: auto;">
            <h2>Input data</h2>
            <hot-table v-model="input_data" :data="input_data" ref="input_data_ref" :autoWrapRow="false"
              :colHeaders="true" :rowHeaders="true" :readOnly="false" style="width: auto; height: auto; overflow: auto"
              licenseKey="non-commercial-and-evaluation"></hot-table>
          </div>

          <h2>Input calculation</h2>
          <hot-table :data="input_calculation" :readOnly="true" ref="input_calculation_ref" :colHeaders="true"
            :rowHeaders="true" style="width: auto; height: auto; overflow: auto"
            licenseKey="non-commercial-and-evaluation"></hot-table>

          <h2>Input graph</h2>
          <hot-table :data="input_graph" :readOnly="true" ref="input_graph_ref" :colHeaders="true" :rowHeaders="true"
            style="width: auto; height: auto; overflow: auto" licenseKey="non-commercial-and-evaluation"></hot-table>

          <v-subheader>Selected city is {{selected_city_column[0]}}</v-subheader>
          <div style="height: 390px; width: 100%; overflow: auto;">
            <l-map :zoom="zoom" :center="selected_city_geodata.slice(1.3)" :attribution="attribution"
              :options="{zoomControl: true}">
              <l-tile-layer :url="url"></l-tile-layer>
              <l-marker :lat-lng="selected_city_geodata.slice(1.3)">
                <l-tooltip>{{selected_city_geodata}}</l-tooltip>
              </l-marker>
            </l-map>
          </div>

          <div style="height: auto; width: 100%; overflow: auto;">
            <h2>Input geodata</h2>
            <hot-table :data="input_geodata" ref="input_geodata_ref" :colHeaders="true" :rowHeaders="true"
              :readOnly="true" style="width: auto; height: auto; overflow: auto"
              licenseKey="non-commercial-and-evaluation"></hot-table>
